//???????????????????????????cookies
function setCookie(name, value) {
    var Days = 30;
    var exp = new Date();
    exp.setTime(exp.getTime() + Days * 24 * 60 * 60 * 1000);
    document.cookie = name + "=" + escape(value) + ";expires=" + exp.toGMTString() +'; path=/';
}
//???????????????????????????cookie
function setCookietime(name, value, time) {
    var msec = getMsec(time); //????????????
    var exp = new Date();
    exp.setTime(exp.getTime() + msec * 1);
    document.cookie = name + "=" + escape(value) + ";expires=" + exp.toGMTString();
}
//?????????????????????????????????,1???=1000??????
function getMsec(DateStr) {
    var timeNum = str.substring(0, str.length - 1) * 1; //????????????
    var timeStr = str.substring(str.length - 1, str.length); //????????????????????????h????????????
    if (timeStr == "s") { //20s??????20???
        return timeNum * 1000;
    }
    else if (timeStr == "h") { //12h??????12??????
        return timeNum * 60 * 60 * 1000;
    }
    else if (timeStr == "d") {
        return timeNum * 24 * 60 * 60 * 1000;
    } //30d??????30???
}

//??????cookies
function delCookie(name) {
    var exp = new Date();
    exp.setTime(exp.getTime() - 1);
    var cval = getCookie(name);
    if (cval != null) {
        document.cookie = name + "=" + cval + ";expires=" + exp.toGMTString();
    }
}
//??????cookies
function getCookie(name) {
    var arr, reg = new RegExp("(^| )" + name + "=([^;]*)(;|$)"); //????????????
    if (arr = document.cookie.match(reg)) {
        return unescape(arr[2]);
    }
    else {
        return null;
    }
}
export default {
    setCookie, delCookie, setCookie, setCookietime, getCookie
}


