import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home/Home'
import Login from '@/components/login/Login'
import Video from '@/components/pageview/video'
import Paper from '@/components/pageview/paper'
import Welcome from '@/components/pageview/welcome'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/login',
      name: 'login',
      component: Login
    },
    {
      path: '/login',
      name: 'Login',
      component: Login
    },
    {
      path: '/home',
      name: 'home',
      redirect: '/home/welcome',
      component: Home,
      children: [
        {
          path: 'welcome',
          name: "welcome",
          component: Welcome,

        },
        {
          path: 'video',
          name: "video",
          component: Video,

        },
        {
          path: 'paper',
          name: "paper",
          component: Paper,

        }
      ]
    }
  ]
})



