<template>

    <el-container class="video" v-loading.body="loading"  element-loading-text="???????????????..." element-loading-background='rgba(255,255,255,.1)'>
      <el-main class="content">
         <el-container>
           <el-header  class="top"> <h3>????????????</h3>
            <p class="btn"><el-button class="signIn" type="primary" @click="refreshData"  >???????????????</el-button></p>
          </el-header>
             <el-main class="tablebox">
  
   <el-table 
    :data="tableData3"
     :highlight-current-row="true"
    border
    style="width: 98%;height:100%"
  
  >
    <el-table-column 
      prop="userInfo"
      label="????????????"
      width="240"
      align="center"
      key="index"
    class-name="column1">
      <template slot-scope="scope">
      <p ><span>??????:</span><span>{{scope.row.userInfo.user_name}}</span></p>
      <p><span>??????:</span><span>{{scope.row.userInfo.user_type}}</span></p>
       <p><span>??????:</span><span>{{scope.row.userInfo.user_subject}}</span></p>
       <p><span>??????:</span><span>{{scope.row.userInfo.user_grade_level}}</span></p>
       <p><span>??????:</span><span>{{scope.row.userInfo.user_level}}</span></p>
       <p><span>??????:</span><span>{{scope.row.userInfo.user_extend}}</span></p>
      </template>
    </el-table-column>
    <el-table-column
      prop="videoInfo"
      label="????????????"
      width="240"
      align="center"
      class-name="column2"
      >
       <template slot-scope="scope"> 
      <p><span>??????:</span><span>{{scope.row.videoInfo.video_title}}</span></p>
      <p><span>??????:</span><span>{{scope.row.videoInfo.video_subject}}</span></p>
       <p><span>??????:</span><span>{{scope.row.videoInfo.video_watch_type}}</span></p>
       <p><span>??????:</span><span>{{scope.row.videoInfo.video_duration}}</span></p>
       <p><span>?????????:</span><span>{{scope.row.videoInfo.video_resolution}}</span></p>
      </template>
    </el-table-column>
    <el-table-column
      prop="showInfo"
      label="??????"
       width="220"
       align="center"
        class-name="column3">
       <template slot-scope="scope">
     <img :src="scope.row.showInfo.video_pic_up_path" @click="openVideo(scope.row.showInfo.video_video_up_path,scope.$index)"  alt="">
      </template>
    </el-table-column>
    <el-table-column
    
      label="????????????"
      align="center"
       width="450"
      class-name="column4">
       <template slot-scope="scope">
          
   <el-radio-group  v-model="result[scope.$index].status">
        <el-radio label="1" >??????</el-radio>
        <el-radio label="2">?????????</el-radio>
   </el-radio-group>
   
<div class="checkarea">
      <el-checkbox-group v-model="result[scope.$index].type_list">
      <el-checkbox label="1" :disabled="result[scope.$index].status==2">??????</el-checkbox>
      <el-checkbox label="2" :disabled="result[scope.$index].status==2">??????</el-checkbox>
      <el-checkbox label="3" :disabled="result[scope.$index].status==2">??????</el-checkbox>
    </el-checkbox-group>
     
  <div class="rightbox">
      <el-select v-model="result[scope.$index].fail_reason" placeholder="????????????????????????" :disabled="result[scope.$index].status==1" >
    <el-option
      v-for="item in errorList"
      :key="item.id"
      :label="item.reason"
      :value="item.id">
    </el-option>
  </el-select>
  <el-input v-model="result[scope.$index].fail_content" v-if='result[scope.$index].fail_reason==107' placeholder="????????????????????????" maxlength="16"></el-input>
  </div>
      
</div>
      </template>
    </el-table-column>
    <el-table-column
    
      label="??????"
        align="center">
       <template slot-scope="scope">
        <el-button @click="submit(scope.$index)"
          size="mini" type="primary"
         >??????</el-button>
       
      </template>
    </el-table-column>
  </el-table>
 </el-main>
         </el-container>
      </el-main>
       <div class="videobox"  v-if="playing">
     
        <video onClick="javascript:clickVideo();" :src="videoUrl" autoplay=true controls="controls"></video>
  </div>
  <div class="mask" v-if="playing">
      <img @click="closevideo" class="closevideo" src="../../assets/img/close.png" alt="">
  </div>
    </el-container>

   
 
 
</template>

<script>
export default {
  data() {
    return {
      videoUrl: "",
      playing: false,
      reason: "",
      access: "-1",
      result: [],
      startlx: false,
      checkList: [],
      errorList: [],
      tableData3: [],
      loading: false,

    };
  },
  watch: {
    result: {
      deep: true,
      handler: () => {}
    },
    startlx(value) {
      if (value) {
        console.log("????????????");
        this.timer = setInterval(() => {
          this.getList();
        }, 3000);
      } else {
        console.log("????????????");
        clearInterval(this.timer);
        this.timer = null;
      }
    }
  },

  mounted() {
    this.getErrorList();
  },
  methods: {
    tableRowClassName({ row, rowIndex }) {
      console.log(row, rowIndex);
      if (rowIndex % 2 == 0) {
        return "black";
      }
    },
    submit(rowIndex) {
      let video_id = this.tableData3[rowIndex].video_id || "";
      let result = this.result[rowIndex];
      let status = result.status;
      let fail_reason = result.fail_reason;
      let fail_content = result.fail_content;
      let type_list = "";
      result.type_list.forEach(item => {
        type_list += item + ",";
      });
      if (status == -1) {
        alert("?????????????????????");
      } else if (status == 2 && fail_reason == "") {
        alert("????????????????????????");
      } else if (status == 2 && fail_reason == "107" && fail_content == "") {
        alert("????????????????????????");
      } else {
        this.loading = true;
        this.$http({
          method: "post",
          url: "/admin/examine_video",
          data: {
            status: status,
            type_list: type_list,
            fail_reason: result.fail_reason,
            fail_content: result.fail_content,
            video_id: video_id
          }
        })
          .then(({ res, ok }) => {
            if (ok) {
              this.tableData3.splice(rowIndex, 1);
              this.result.splice(rowIndex, 1);
            }
          })
          .catch(error => {})
          .finally(() => {
            this.loading = false;
          });
      }
    },
    refreshData() {
      if (this.tableData3.length > 0) {
        alert(
          "?????????" + this.tableData3.length + "??????????????????????????????????????????"
        );
        return;
      }
      this.getList();
    },
    getErrorList() {
      this.loading = true;
      this.$http({
        method: "get",
        url: "/admin/fail_reason_list"
      })
        .then(({ res, ok }) => {
          if (ok) {
            this.errorList = res;
          }
        })
        .catch(error => {
          alert("????????????????????????");
        })
        .finally(() => {
          this.loading = false;
        });
    },
    getList() {
      this.loading = true;
      this.$http({
        url: "/admin/current_video_list"
      })
        .then(({ res, ok }) => {
          if (ok) {
            if (res.length == 0) {
              this.result = [];
              this.tableData3 = [];
              this.startlx = true;
            } else {
              let arr = [];
              for (let i = 0; i < res.length; i++) {
                arr.push({
                  status: -1,
                  type_list: [],
                  fail_reason: "",
                  fail_content: ""
                });
              }
              this.result = arr;
              this.tableData3 = res;
              this.startlx = false;
            }
          }
        })
        .catch(error => {
          console.log(error);
          alert("????????????????????????");
        })
        .finally(() => {
          this.loading = false;
        });
    },
    openVideo(url) {
      if (!!url) {
        this.videoUrl = url;
        this.playing = true;
      } else {
        alert("??????????????????");
      }
    },
    closevideo() {
      this.videoUrl = "";
      this.playing = false;
    }
  },
  destroyed() {
    this.timer && clearInterval(this.timer);
  }
};
</script>

<style lang="less" rel="stylesheet/stylus"  scoped>
.el-container {
  padding: 0;
  height: 100%;
  .mask {
    height: 100%;
    width: 100%;
    position: fixed;
    left: 0;
    top: 0;
    z-index: 80;
    opacity: 0.6;
    background: #000;
    img {
      position: absolute;
      right: 120px;
      top: 100px;
      width: 40px;
    }
  }
  .videobox {
    position: absolute;
    height: 882px;
    width: 416px;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    z-index: 100;
    //background: red;
    background: url("../../assets/img/bgiphone.png") no-repeat center;
    background-size: 427px 881px;
    background-color: #000;
    border-radius: 55px;
    video {
      margin-top: 116px;
      height: 667px;
      width: 375px;
    }
  }
  .content {
    padding: 0;
    .top {
      display: flex;
      background: #fff;
      height: 110px !important;
      color: #333;
      justify-content: center;
      align-items: center;
      flex-direction: column;

      h3 {
        color: #808080;
        letter-spacing: 5px;
        text-align: center;
        font-size: 26px;
      }
      .btn {
        align-self: flex-end;
        margin-right: 5px;
      }
    }
  }
  .tablebox {
    padding: 0;

    .el-table {
      font-size: 16px;
      margin: 10px 0 0 10px;
  
      .el-table-column {
        font-size: 44px;
      }
    }

    .column1,
    .column2 {
      p {
        display: flex;
        height: 30px;
        line-height: 30px;
        span:nth-child(1) {
          margin-right: 5px;
        }
      }
    }
    .column3 img {
      width: 200px;
      height: 200px;
    }
    .column4 {
      .el-radio-group {
        display: flex;
        justify-content: space-around;
        width: 57%;
        margin-left: 0px;
      }
      .checkarea {
        margin-top: 10px;
        display: flex;

        justify-content: space-around;
        .rightbox {
          width: 66%;
          .el-select {
            margin-top: 8px;
          }
          .el-input {
            margin-top: 10px;
            width: 220px;
          }
        }
        .el-checkbox-group {
          height: 100px;

          display: flex;
          justify-content: space-around;
          flex-direction: column;
          .el-checkbox {
            margin-left: 0;
          }
        }
      }
    }
  }
}
</style>



