<script>
export default {
  data() {
    return {
      access: 0,
      reason: "",
      checkList: [],
      options: [
        {
          value: "??????1",
          label: "?????????"
        },
        {
          value: "??????2",
          label: "?????????"
        },
        {
          value: "??????3",
          label: "?????????"
        },
        {
          value: "??????4",
          label: "?????????"
        },
        {
          value: "??????5",
          label: "????????????"
        }
      ],
      tableData3: [
        {
          userInfo: {
            a: "??????11111",
            b: "??????",
            c: "??????",
            d: "??????",
            e: "??????",
            f: "??????"
          },
          name: {
            a: 111,
            b: 222,
            c: 333,
            d: 444,
            e: 555,
            f: 666
          },
          address: "?????????????????????????????? 1518 ???"
        }
      ]
    };
  },
  watch: {
    access(value) {
      this.checkList = [];
      this.reason = "";
    }
  },
  methods: {},
  mounted() {}
};
