<style lang="less" rel="stylesheet/stylus"  scoped>
.el-container {
  height: 100%;
}
.el-header {
  background-color: #409eff;
  color: #333;
  line-height: 60px;

  h3 {
    text-align: left;
    font-size: 26px;
  }
}

.el-main {
  padding: 0;
  background: #fff;

  .el-header {
    background: #fff;
    height: 200px;
    h3 {
      text-align: center;
      color: #808080;
      letter-spacing: 5px;
    }
  }
  .tablebox {
    .btn {
      margin-top: 20px;
      text-align: right;
      padding-right: 50px;
    }
    .el-table {
      font-size: 16px;
      margin: 10px;
      .el-table-column {
        font-size: 44px;
      }
    }
  }
  .column1,
  .column2 {
    p {
      display: flex;
      height: 30px;
      line-height: 30px;
      span:nth-child(1) {
        margin-right: 5px;
      }
    }
  }
  .column3 img {
    width: 200px;
  }
  .column4 {
    .el-radio-group {
      display: flex;
      justify-content: space-around;
      width: 72%;
    }
    .checkarea {
      margin-top: 10px;
      display: flex;

      justify-content: space-around;
      .el-checkbox-group {
        height: 100px;
        display: flex;
        justify-content: space-around;
        flex-direction: column;
        .el-checkbox {
          margin-left: 0;
        }
      }
    }
  }
}
</style>



