<template>
  <div class="home">
<el-container>
  <el-header> <h3>??????????????????</h3> <div @click="exit">????????????</div> </el-header>
  <el-container>
    <el-aside width="200px">
     
      <div class="userInfo">
        <img class="icon" src="../../assets/img/user.png" alt=""><span class="name">{{account}}</span>
      </div>
      <div class="list">
        <p @click="changeview(index)" :class="[index==clickIndex?'check':'']"  v-for="(item,index) in sliderList" :key="index" >{{item}} <i class='el-icon-arrow-right'></i></p>
       
      </div>
    </el-aside>
    <el-container>
      <el-main >
       <keep-alive>
         <router-view></router-view>
       </keep-alive>
      </el-main>
    </el-container>
  </el-container>
</el-container>
  </div>
</template>

<script>
export default {
  data() {
    return {
      account: sessionStorage.getItem("account") || "????????????",
      sliderList: ["????????????", "????????????", "????????????", "????????????"],
      clickIndex: sessionStorage.getItem("clickIndex") || -1
    };
  },

  mounted() {},
  methods: {
    exit() {
      this.$router.replace({ path: "/login" });
      sessionStorage.clear();
    },
    changeview(index) {
      this.clickIndex = index;
      sessionStorage.setItem("clickIndex", index);
      if (index == 0) {
        this.$router.replace({ path: "/home/video" });
      } 
    }
  }
};
</script>

<style lang="less" rel="stylesheet/stylus"  scoped>
.home {
  height: 100%;
  background: #d2d7dd;
}
.el-container {
  height: 100%;
}
.el-header {
  background-color: #409eff;
  color: #fff;
  line-height: 60px;
  display: flex;
  justify-content: space-between;
  h3 {
    text-align: left;
    font-size: 26px;
    display: inline-block;
    width: 50%;
    letter-spacing: 5px;
  }
  div {
    margin-right: 50px;
    display: inline-block;
    font-size: 18px;
    color: #fff;
    background: url("../../assets/img/exit.png") no-repeat left center;
    background-size: 28px;
    padding-left: 32px;
  }
}

.el-aside {
  color: #333;
  background-color: #409eff;
  border-top: 1px solid #137fef;
  .userInfo {
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 10px;
    .icon {
      width: 50px;
      height: 50px;
      margin-right: 20px;
    }
    .name {
      font-size: 18px;
      color: #fff;
    }
  }
  .list {
    font-size: 18px;
    p {
      height: 40px;
      line-height: 40px;
      background: #fff;
      margin-bottom: 1px;
      border-right: 2px solid #409eff;
      i {
        margin-left: 60px;
      }
    }
    .check {
      color: #409eff;
    }
  }
}
.el-main {
  background: #fff;
  padding: 0;
}
</style>



